	closeKeys(type="input")
	{
		document.querySelector(type).addEventListener('keydown', function (e) {
			if (e.keyCode === 13) {
			document.querySelector(type).setAttribute('readonly', 'readonly');
			setTimeout(function () { document.querySelector(type).removeAttribute('readonly') }, 500);
			}
			});
	}
